<template>
  <div>
    <label>
      <input type="textarea" v-model="message" />
      <p>message:{{message}}</p>
      <textarea v-model="message1"></textarea>
      <p>message1:{{message1}}</p>

      <label>
        <input type="checkbox" name="hobby" value="篮球" v-model="message2" />篮球
      </label>
      <label>
        <input type="checkbox" name="hobby" value="足球" v-model="message2" />足球
      </label>
      <label>
        <input type="checkbox" name="hobby" value="羽毛球" v-model="message2" />羽毛球
      </label>
      <p>message2:{{message2}}</p>
    </label>

    <label>
      <input type="radio" v-model="message3" value="青菜" />青菜
    </label>
    <label>
      <input type="radio" v-model="message3" value="胡萝卜" />胡萝卜
    </label>
    <label>
      <input type="radio" v-model="message3" value="土豆" />土豆
    </label>
    <p>message3:{{message3}}</p>

    <span>地区</span>
    <select v-model="message4">
      <option disabled value>-</option>
      <option>北京</option>
      <option>浙江</option>
      <option>上海</option>
    </select>
    <p>message4:{{message4}}</p>

    <span>地区</span>
    <select v-model="message5">
      <option
        v-for="option in options"
        v-bind:value="option.value"
        :key="option.value"
      >{{option.text}}</option>
    </select>
    <p>message5:{{message5}}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      message: undefined,
      message1: undefined,
      message2: [],
      message3: undefined,
      message4: undefined,
      message5: undefined,
      options: [
        { text: "北京", value: "beijing" },
        { text: "浙江", value: "zhejaing" },
        { text: "上海", value: "shanghai" },
      ],
    };
  },
};
</script>

<style>
label {
  user-select: none;
}
</style>
